<template>
  <div class="nearby">
    <div class="nearby__title">附近店铺</div>
    <router-link v-for="item in nearbyList" :key="item._id" :to="`/shop/${item._id}`">
      <ShopInfo :item="item"/>
    </router-link>
  </div>
  <Toast v-if="toastInfo.show" :message="toastInfo.message"/>
</template>

<script>
import { reactive } from 'vue'
import { get } from '@/utils/request'
import ShopInfo from '@/components/ShopInfo.vue'
import Toast, { useToastEffect } from '@/components/Toast.vue'

export default {
  name: 'NearbyView',
  components: {
    Toast,
    ShopInfo
  },
  setup () {
    const nearbyList = reactive([])
    const {
      toastInfo,
      showToast
    } = useToastEffect()
    const getNearByList = async () => {
      try {
        const result = await get('/api/shop/hot-list')
        if (result?.errno !== 0) return showToast(result.desc)
        nearbyList.push(...result.data)
        nearbyList.push(...result.data)
        nearbyList.push(...result.data)
        nearbyList.push(...result.data)
      } catch (e) {
        showToast(e.message)
      }
    }
    getNearByList()

    return {
      nearbyList,
      toastInfo
    }
  }
}
</script>

<style lang="scss" scoped>
.nearby {
  &__title {
    margin: .16rem 0 .02rem;
    font-size: .18rem;
  }

  a {
    text-decoration: none
  }
}
</style>
